import React from "react";
import pngCopyright from "../../images/copyright.png";
import styled, { css } from "styled-components";
import Icon from "../Icon";
import Juejin from "../Icon/Juejin";
import { Link } from "gatsby";
import IconEMail from "../Icon/EMail";

const DocInfoStyle = styled.div`
  @media print {
    .issues,
    .right,
    .connect {
      display: none;
    }
  }

  ${({ theme: { colors: c } }) => css`
    margin: 70px 0;
    padding: 1em;
    background-color: ${c.bg.refer};
    border: 1px solid ${c.primary.main};
    box-shadow: 0px 1px 5px ${c.primary.refer};

    ul {
      list-style: none;
      padding-left: 0;
      font-size: 14px;
      margin: 1em 0;
    }
    li {
      line-height: 1.5em;
    }
    a {
      color: ${c.link.main};
      text-decoration: none;
      &:hover,
      &:active,
      &:focus {
        filter: brightness(1.3);
        text-decoration: underline;
      }
    }
    .middle {
      vertical-align: middle;
      margin: 4px;
    }
    .right {
      float: right;
    }
  `}
`;

export interface DocInfoProps {
  //  git commit 
  lastModify?: string;
  // 
  sourceLink?: string;
  // 
  tags?: string[];
  // 
  archives?: string[];
  // 
  historyLink?: string;
  // 
  createAt: string;
  /**  */
  path: string;
}

export default function DocInfo({ lastModify, sourceLink, archives, tags, historyLink, createAt, path }: DocInfoProps) {
  const crTip = "-- 4.0  (CC BY-NC-ND 4.0)";

  const tagsLink = tags?.map((v, i) => (
    <Link key={i} to={`/search?keyword=${v}`}>
      {v} &nbsp;
    </Link>
  ));

  const archivesLink = archives?.map((v, i) => (
    <Link key={i} to={`/search?keyword=${v}`}>
      {v} &nbsp;
    </Link>
  ));

  return (
    <DocInfoStyle>
      <h2></h2>
      <ul className="info">
        <li className="right">
          <a href={historyLink} target="_blank">
            
          </a>
          {" - "}
          <a href={sourceLink} target="_blank">
            
          </a>
        </li>
        <li>
          {createAt} - {lastModify}
        </li>
        <li>
          
          <a href="https://creativecommons.org/licenses/by-nc-nd/4.0/" target="_blank">
            <img className="middle" src={pngCopyright} alt={crTip} title={crTip} />
            {crTip}
          </a>
        </li>
        <li className="connect">
          
          <a href="mailto:842925337@qq.com" target="_blank">
            <Icon size="32px" fill="blue" vertical="middle" children={<IconEMail color="#0096d3" />} />
            <span>842925337@qq.com</span>
          </a>
          
          <a href="https://blog.csdn.net/qq_42881675" target="_blank">
            <span>csdn</span>
          </a>
        </li>
        <li>
          <a href={path}>{path}</a>
        </li>
      </ul>
      <ul>
        {tagsLink?.length > 0 && <li key="label">{tagsLink}</li>}
        {archivesLink?.length > 0 && <li key="archives">{archivesLink}</li>}
      </ul>
    </DocInfoStyle>
  );
}
